import './Introduction.css'


export default function Intro(props) {
	const { status, moveToNext, onOpen, idx, toRight } = props
	if(status === 'openning' && onOpen){
		setTimeout(() => onOpen(idx), 1300)
	}
	const animationClass = status === 'closing' ? " closing" + (toRight ? '-right' : '') : ""
	return (
		<div className={"background" + animationClass + (status === 'openning' ? " openning" : "")}>
			<div className="blur-background"></div>
			<div className={"content" + (status === 'closing' ? ' hidden' : '') + (status === 'openning' ? " openning" : "")}>
				<div className="heading">–ß—Ç–æ —ç—Ç–æ –∑–∞ —Å–∞–π—Ç?</div>	
				<div className="text">
					<p>–≠—Ç–æ –¥–æ–ø–æ–ª–Ω–µ–Ω–∏–µ –∫ —Ä–µ–∑—é–º–µ –≤–µ–±-—Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–∞.
					<br/>–ó–¥–µ—Å—å –≤—ã –Ω–∞–π–¥–µ—Ç–µ –ø—Ä–∏–º–µ—Ä—ã –º–æ–µ–≥–æ –∫–æ–¥–∞, —á—Ç–æ –ø–æ–º–æ–∂–µ—Ç –≤–∞–º –≤ –ø—Ä–æ—Ü–µ—Å—Å–µ —Ä–µ–∫—Ä—É—Ç–∏–Ω–≥–∞.
					<br/>–ó–¥–µ—Å—å –Ω–µ–º–Ω–æ–≥–æ —Ä–∞–±–æ—Ç - —è —Å–¥–µ–ª–∞–ª –∞–∫—Ü–µ–Ω—Ç –Ω–∞ —è—Å–Ω–æ—Å—Ç–∏ –∏ –ø—Ä–æ—Å—Ç–æ—Ç–µ –∫–æ–¥–∞.</p>
				</div>
				<div className="links">
					<a href="https://hh.ru/resume/68f87c1fff0979f6120039ed1f566c4361397a" target="_blank" rel="noreferrer"><b>–ü–µ—Ä–µ–π—Ç–∏ –∫ —Ä–µ–∑—é–º–µ</b></a>
				</div>			
				<div className="footer">
				<button className="next" onClick={moveToNext}>–ù–∞—á–∞—Ç—å</button></div>
			</div>	
		</div>
		)
}